import React, { useRef,useState } from 'react';
import HTMLFlipBook from 'react-pageflip';
import './Story.css'; // Import your own CSS file for custom styling

const Page = React.forwardRef((props, ref) => {
    return (
        <div className="demoPage page" ref={ref}>
        
            {/* <h1>Page Header</h1> */}
            {/* <p>{props.children}</p> */}
            <div className = "s-page">
                <div className = "txt-container">
                    
Once upon a time in a vast grassland, a clever and agile rabbit named Fluffy lived peacefully among the other animals. Fluffy was known for his quick wit and remarkable speed, which helped him navigate the grassy meadows and stay out of harm's way.
                </div>
                <div className='img-container'>
                  <div className='tape'></div>
                    <img className = 'img' src = "https://res.cloudinary.com/sidd293/image/upload/v1685034450/The-Foolish-Lion-And-The-Clever-Rabbit-Story-With-Moral-For-Kids_uvlfzu.jpg"></img>
                    <div className='ltape'></div>
                </div>
            </div>
          
        </div>
    );
});
const Story = () => {
    const flipBookRef = useRef(null);
    const [currentPage, setCurrentPage] = useState(0);
    const [text, setText] = useState("Once upon a time in a vast grassland, a clever and agile rabbit named Fluffy lived peacefully among the other animals. Fluffy was known for his quick wit and remarkable speed, which helped him navigate the grassy meadows and stay out of harm's way.");
    const handleSpeak = () => {
        if ('speechSynthesis' in window) {
          const message = new SpeechSynthesisUtterance(text);
          message.volume = 1; // Volume (0 to 1)
          message.rate = 1; // Speech rate (0.1 to 10)
          message.pitch = 1; // Speech pitch (0 to 2)
          message.lang = 'en-GB'; // Language code (e.g., 'en-US', 'en-GB')
          window.speechSynthesis.speak(message);
        } else {
          alert('Text-to-speech is not supported in this browser.');
        }
      };
    const handlePrevPage = () => {
        console.log(flipBookRef.current.pageFlip().flipPrev()   )
        // flipBookRef.current.pageFlip()
    //   if (flipBookRef.current && currentPage > 0) {
    //     setCurrentPage(currentPage - 1);
    //     flipBookRef.current.flipToPage(currentPage - 1);
    //   }
    };
  
    const handleNextPage = () => {
        flipBookRef.current.pageFlip().flipNext()
    //   if (flipBookRef.current && currentPage < 3) { // Assuming 4 pages in total
    //     setCurrentPage(currentPage + 1);
    //     flipBookRef.current.flipToPage(currentPage + 1);
    //   }
    };
  
    const onPageFlip = (e) => {
      console.log('Flipped to page:', e);
      setCurrentPage(e);
    };
  return (
    <div>
      <div className="buttons-container">

        <img src  = "https://res.cloudinary.com/sidd293/image/upload/v1685035329/img.icons8_yntpsl.png" className = "btn l" onClick={handlePrevPage}></img>
        <img src  = "https://res.cloudinary.com/sidd293/image/upload/v1685035739/download_vlziuo.png" className = "btn s" onClick={handleSpeak}></img>
      
        <img src = "https://res.cloudinary.com/sidd293/image/upload/v1685035329/img.icons8_yntpsl.png" className = "btn r" onClick={handleNextPage}></img>
      </div>
      <HTMLFlipBook
         width={550}
         height={800}
         size="stretch"
         minWidth={415}
         maxWidth={500}
         minHeight={400}
         maxHeight={1033}
         maxShadowOpacity={0.5}
        //  showCover={true}
         mobileScrollSupport={true}
        //  onFlip={this.onPage}
        //  onChangeOrientation={this.onChangeOrientation}
        //  onChangeState={this.onChangeState}
         className="demo-book"
        onFlip={onPageFlip}
        ref={flipBookRef}
      >
 <div className = "titlePg" number="0"></div>
      <Page number="1">Page text</Page>
            <Page number="2">Page text</Page>
            <Page number="3">Page text</Page>
            <Page number="4">Page text</Page>
            <Page number="5">Page text</Page>
            <Page number="6">Page text</Page>
            <Page number="7">Page text</Page>
            <Page number="8">Page text</Page>
      </HTMLFlipBook>
    </div>
  );
};

export default Story;
